@import 'variables';
@import 'mixins';

* {
	margin: 0;
	padding: 0;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font-family: verdana;
	font-size: 16px; }

body, html {
	background: $color-1;
	color: $color-11;
	height: 100%; }

h1, h2 {
	color: $color-3;
	padding: 20px 0;
	font-size: 48px; }

input {
	@extend %rounded;
	&[type=text] {
		height: $button-height; } }

button {
	@extend %rounded;
	@extend %no-select;
	color: $color-1;
	background: $color-3;
	padding: 10px;
	height: $button-height;
	cursor: pointer;
	outline: none;
	border: none;
	&:hover {
		background: $color-4; } }

*:hover, button, a {
	@extend %trans-effect; }

.container-main {
	max-width: 720px;
	margin: 0 auto 40px auto;
	.container {
		@extend %rounded;
		@extend %shadow;
		background: $color-10;
		padding: 20px;
		margin-top: 20px; } }

.loader-top {
	padding: 50px 0;
	text-align: center; }

.number-format {
	background: inherit;
	color: inherit;
	border: none;
	text-align: right;
	width: 80px;
	height: 100%; }

.align-right {
	text-align: right; }

button.btn-edit {
	background: $color-5;
	&:hover {
		transition: all 0.5s ease;
		background: $color-6; } }

button.btn-delete {
	background: $color-7;
	&:hover {
		transition: all 0.5s ease;
		background: $color-8; } }

/******************************************************************************/
/* MEDIA QUERY - MAX 760 */
/******************************************************************************/

@media screen and (max-width: 760px) {
	.container-main {
		margin: 0 10px; } }

/******************************************************************************/
/* MEDIA QUERY - MAX 700 */
/******************************************************************************/

@media screen and (max-width: 700px) {
	%rounded-no {
		-moz-border-radius: 0;
		-webkit-border-radius: 0;
		border-radius: 0; }

	%shadow-no {
		box-shadow: none; }

	h1 {
		padding: 0 10px; }

	.container-main {
		margin: 0;
		.container {
			width: 100%;
			@extend %rounded-no; } } }
